.wrapper {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.label {
  font-size: 12px;
  color: var(--muted);
}

.input {
  background: rgba(255, 255, 255, 0.03);
  color: var(--text);
  border: 1px solid rgba(255, 255, 255, 0.06);
  padding: 10px 12px;
  border-radius: 8px;
  outline: none;
  transition: box-shadow 150ms ease, border-color 150ms ease;
  box-sizing: border-box; /* ensure padding included in width */
  width: 100%; /* ensure inputs expand to fill grid cell; avoids Firefox min-width issues */
}

.input:focus {
  box-shadow: 0 4px 14px rgba(14, 165, 164, 0.12);
  border-color: rgba(14, 165, 164, 0.7);
}

.inputError {
  border-color: var(--danger);
  background: rgba(255, 107, 107, 0.03);
}

.inputError:focus {
  box-shadow: 0 6px 20px rgba(255, 107, 107, 0.06);
  border-color: var(--danger);
}

/* ensure number input doesn't overflow in Firefox */
input[type='number'] {
  min-width: 0;
}

@media (max-width: 480px) {
  .input {
    padding: 12px;
    font-size: var(--fs-sm, 15px);
  }
  .label {
    font-size: var(--fs-xs, 13px);
  }
}

/* compact input variant for tight lists */
.inputCompact {
  padding: 8px;
  font-size: var(--fs-xs, 13px);
}
