.select {
  background: transparent;
  border: 1.2px solid transparent;
  color: var(--color-text, #e6e6e6);
  padding: 0 8px;
  height: 28px;
  border-radius: 6px;
}

.selectCompact {
  min-width: 64px;
  padding: 0 6px;
  height: 24px;
  font-size: 12px;
}

.colored {
  border-radius: 999px;
  padding: 4px 10px;
  height: 28px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  text-transform: uppercase;
}

.coloredOpen {
  background: rgba(110, 231, 183, 0.08);
  color: #69f0ae;
  border: 1px solid rgba(110, 231, 183, 0.14);
}

.coloredClosed {
  background: rgba(255, 255, 255, 0.02);
  color: var(--muted);
  border: 1px solid rgba(255, 255, 255, 0.04);
}

.coloredFilled {
  background: rgba(203, 213, 225, 0.06);
  color: #60a5fa;
  border: 1px solid rgba(96, 165, 250, 0.08);
}

.error {
  outline: 1px solid rgba(255, 107, 107, 0.12);
  box-shadow: 0 0 0 3px rgba(255, 107, 107, 0.04);
}
