  --accent-strong: rgba(255, 45, 212, 0.18);
  --glass: rgba(255, 255, 255, 0.02);
  --glass-strong: rgba(255, 255, 255, 0.05);
}

:root[data-theme='forest'] {
  /* deep forest greens */
