  --accent-glass: rgba(121, 194, 103, 0.08);
  --accent-strong: rgba(121, 194, 103, 0.18);
  --glass: rgba(255, 255, 255, 0.02);
  --glass-strong: rgba(255, 255, 255, 0.04);
}

:root[data-theme='sunrise'] {
  /* warm sunrise gradient vibe */
  --bg: #0c0a10;
  --surface: #130f15;
  --text: #fff2ea;
  --muted: #c8b6b2;
  --color-text: #fff6f2;
  --color-primary: #ff7a59; /* coral */
  --accent: #ffd166; /* sunrise yellow */
