  --accent-strong: rgba(255, 122, 89, 0.18);
  --glass: rgba(255, 255, 255, 0.02);
  --glass-strong: rgba(255, 255, 255, 0.04);
}

:root[data-theme='mono'] {
  /* monochrome high-contrast */
