.list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 8px;
  max-height: 68vh;
  overflow: auto;
}

.item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.01), rgba(255, 255, 255, 0.006));
  border: 1px solid rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  cursor: pointer;
  transition: transform 0.12s ease, box-shadow 0.12s ease, background 0.12s ease;
}

.item:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(2, 8, 23, 0.45);
  background: linear-gradient(180deg, rgba(34, 193, 195, 0.02), rgba(255, 255, 255, 0.008));
}

.selected {
  border-color: rgba(124, 92, 255, 0.9);
  /* outer glow + thin accent ring to emulate highlighted outline */
  box-shadow: 0 6px 24px rgba(124, 92, 255, 0.16), 0 0 0 1px rgba(124, 92, 255, 0.18);
  background: linear-gradient(90deg, rgba(124, 92, 255, 0.06), rgba(34, 193, 195, 0.02));
}

.rowLeft {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.symbol {
  font-weight: 700;
  color: var(--text);
}

.meta {
  font-size: 12px;
  color: var(--muted);
}

.rowRight {
  display: flex;
  align-items: center;
  gap: 8px;
}

.side {
  border-radius: 999px;
  font-weight: 700;
  font-size: 12px;
  text-transform: uppercase;
  /* base neutral appearance similar to SideSelect */
  background: transparent;
  color: var(--color-text);
  border: 1px solid transparent;

  display: inline-flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  width: 72px; /* fixed width for consistent layout */
  height: 28px; /* align with SideSelect height */
  padding: 4px 8px; /* similar breathing room */
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Colored pill variants (light tint background + colored border/text) */
.sideLong {
  /* rgba(15,157,88,0.10) using --color-long-600 (#0F9D58) */
  background: rgba(15, 157, 88, 0.1);
  color: var(--color-long-600);
  border: 1px solid rgba(15, 157, 88, 0.18);
}

.sideShort {
  /* rgba(219,68,55,0.10) using --color-short-600 (#DB4437) */
  background: rgba(219, 68, 55, 0.1);
  color: var(--color-short-600);
  border: 1px solid rgba(219, 68, 55, 0.18);
}

/* Compact variant for tighter layouts (matches SideSelect.selectCompact.colored) */
.sideCompact {
  min-width: 56px;
  height: 24px;
  padding: 2px 8px;
  font-size: 12px;
  font-weight: 700;
}

/* make badges automatically compact on small screens */
@media (max-width: 420px) {
  .side,
  .status {
    width: 56px;
    height: 24px;
    padding: 2px 8px;
    font-size: 12px;
  }
}

/* focus visible for keyboard users */
.item:focus-visible {
  outline: 3px solid rgba(124, 92, 255, 0.12);
  outline-offset: 2px;
}

/* scrollbar subtle */
.list::-webkit-scrollbar {
  width: 8px;
}
.list::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.03);
  border-radius: 6px;
}

.status {
  border-radius: 999px;
  font-weight: 700;
  font-size: 12px;
  text-transform: uppercase;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 72px; /* match side badge width */
  height: 28px;
  padding: 4px 8px;
  box-sizing: border-box;
  text-align: center;
  white-space: nowrap;
}

.statusOpen {
  background: rgba(110, 231, 183, 0.08);
  color: #69f0ae;
  border: 1px solid rgba(110, 231, 183, 0.14);
}

.statusClosed {
  background: rgba(255, 255, 255, 0.02);
  color: var(--muted);
  border: 1px solid rgba(255, 255, 255, 0.04);
}

.statusFilled {
  background: rgba(96, 165, 250, 0.06);
  color: #60a5fa;
  border: 1px solid rgba(96, 165, 250, 0.08);
}

@media (max-width: 720px) {
  .list {
    padding: var(--space-2, 8px);
    max-height: 50vh;
  }
  .item {
    padding: 8px;
    gap: 8px;
  }
  .rowRight {
    gap: 6px;
  }
  .symbol {
    font-size: var(--fs-sm, 14px);
  }
  .meta {
    font-size: var(--fs-xs, 13px);
  }
}

@media (max-width: 420px) {
  .item {
    flex-direction: column;
    align-items: flex-start;
    padding: 10px;
  }
  .rowRight {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  .side,
  .status {
    width: auto;
    height: auto;
    padding: 6px 8px;
  }
  .symbol {
    font-size: var(--fs-sm, 14px);
  }
  .meta {
    font-size: var(--fs-xs, 13px);
  }
}

/* compact list modifier that pages can add when they want tighter spacing */
.compact .item {
  padding: 6px;
}
